@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
}

.App{
  min-height: 100vh;
  display: grid;
  background-image: linear-gradient(to bottom, #2c3e50, #34495e);

}

.weather{
  place-self: center;
  padding: 40px;
  border-radius: 10px;
  background-image: linear-gradient(45deg, #1e272e, #2d3436);
  display: flex;
  flex-direction: column;
  align-items: center;

}

.searchbar{
  display: flex;
  align-items: center;
  gap: 12px;
}

.searchbar input[type="text"]{
  height: 40px;
  border: none;
  outline: none;
  border-radius: 40px;
  padding-left: 25px;
  color: #626262;
  background-color: #ebfffc;
  font-size: 18px;
}

.searchbar img{
  width: 38px;
  padding: 12px;
  border-radius: 50%;
  background: #ebfffc;
  cursor: pointer

}

.weather-icon{
  width: 100px;
  margin: 30px 0;
}

.temperature{
  color:#fff;
  font-size: 64px;
  line-height: 1;
}

.location{
  color:#fff;
  font-size: 40px;

}

.weather-data{
  width:100%;
  margin-top: 40px;
  color:#fff;
  display: flex;
  justify-content: space-between;
}

.weather-data .col{
  display: flex;
  align-items: flex-start;
  gap:12px;
  font-size: 22px;
}

.weather-data span{
  display: block;
  font-size: 16px;
}

.weather-data .col img{
  width:26px;
  margin-top:10px;
}
